.item {
    display: flex;
    align-items: center;
    gap: 10px;
    padding: 10px;
    border-radius: 5px;
    width: 250px;
    box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%),
        0px 2px 1px rgb(0 0 0 / 20%);
}

.status {
    border-radius: 50%;
    width: 15px;
    height: 15px;
}

.isOnline {
    background-color: var(--isOnlineColor);
}

.isOffline {
    background-color: var(--isOfflineColor);
}

.avatar {
    display: block;
    background-color: #e9edd5;
    border-radius: 5px;
    padding: 10px;
}

.name {
    font-size: 20px;
    font-weight: 500;
}