.statistics {
    margin: 0 auto;
    width: 400px;
    box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%),
        0px 2px 1px rgb(0 0 0 / 20%);
    border-radius: 5px;
    background-color: #fff;
}

.title {
    font-size: 28px;
    font-weight: 500;
    text-align: center;
    padding-top: 20px;
}

.statList {
    display: flex;
    margin-top: 20px;
    border-top: solid 1px #d4d3d2;
}

.item {
    display: flex;
    flex-direction: column;
    width: calc(100% / 5);
    align-items: center;
}

.item:not(:last-child) {
    border-right: solid 1px #d4d3d2;
}

.label {
    margin: 10px 0 10px 0;
    color: #fff;
}

.percentage {
    color: #fff;
    padding-bottom: 10px;
    font-size: 22px;
    font-weight: 500;
}