.profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 1px 3px rgb(0 0 0 / 12%), 0px 1px 1px rgb(0 0 0 / 14%),
        0px 2px 1px rgb(0 0 0 / 20%);
    width: 400px;
    background-color: #fff;
    border-radius: 5px;
    margin: 50px auto 50px auto;
}

.avatar {
    width: 150px;
    border-radius: 50%;
    background-color: #e9edd5;
    margin-top: 50px;
}

.description {
    text-align: center;
}

.name {
    margin-top: 30px;
    font-size: 24px;
    font-weight: 500;
}

.tag {
    margin-top: 10px;
    color: #9d9e99;
    font-size: 18px;
}

.location {
    margin-top: 10px;
    color: #9d9e99;
    font-size: 20px;
}

.stats {
    display: flex;
    background-color: #f7f7f7;
    margin-top: 30px;
    border-top: solid 1px #d4d3d2;
    width: 100%;
}

.stats__item {
    display: flex;
    flex-direction: column;
    width: calc(100% / 3);
    padding: 20px 0 20px 0;
    align-items: center;
}

.stats__item:not(:last-child) {
    border-right: solid 1px #d4d3d2;
}

.label {
    margin-bottom: 10px;
    color: #878787;
    font-size: 18px;
}

.quantity {
    font-size: 22px;
    color: #0a0a0a;
    font-weight: 500;
}